@import "./../../../bower_components/prism/themes/prism";

html, body {
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Open Sans', sans-serif;
}

a {
  color: #ad4844;
}

a:hover {
  color: #a61717;
}

h1 {
  text-transform: uppercase;
  font-weight: 900;
  font-size: 30px;
  border-bottom: 1px solid #ad4844;
  margin: 13px 0px 30px;
  padding-bottom: 8px;
}

h2 {
  text-transform: uppercase;
  font-weight: 300;
  font-size: 25px;
  border-bottom: 1px dashed #CCC;
  padding-bottom: 7px;
  margin: 50px 0px 15px;
}

pre {
  border-radius: 2px;
  background-color: #fafafa;
  border: 1px dashed #ccc;
  margin: 20px 0px;
}

ul {
  margin: 30px 30px;
  padding: 0px 15px;
  list-style: none;
  border-left: 2px solid #ad4844;
}

li {
  margin-bottom: 10px;
  padding: 2px 0px;
}

.main {
  border-bottom: 2px dashed #ad4844;
  padding: 5px 0px;
  margin-bottom: 40px;
}

.main .container {
  display: flex;
  align-items: flex-end;
}

.brand {
  flex: 1 1 auto;
  display: block;
  height: 70px;
}

.brand img {
  max-height: 100%;
}

.alert-danger {
  background-color: #f1e4df;
  color: #ad4844;
  border: 1px dashed #c2786b;
}

.side-menu {
  padding-right: 15px;
}

.side-menu h1 {
  font-size: 20px;
  font-weight:700;
  text-align: center;
  text-transform: uppercase;
  margin-top:0px;
  margin-bottom: 25px;
  border: none;
}

.side-docs-nav {}


.side-docs-nav h4 {
  font-size: 16px;
  font-weight:300;
  text-align: center;
  text-transform: uppercase;
  border-bottom: 1px dashed #CCC;
  padding: 0px 0px 7px;
  margin-top: 30px;
  cursor: pointer;
}

.side-docs-nav ul {
  list-style-type: none;
  margin: 0px;
  padding: 0px 15px;
  border: none;
}

.side-docs-nav li {
  border: none;
  padding: 0px;
  margin: 0px;
}

.side-docs-nav li a {
  display: inline-block;
  margin-bottom: 8px;
}

.docs-wrapper {
  margin-bottom: 120px;
}


h2 {
  cursor: pointer;
}
h2:after {
  content: "#";
  margin-left: 10px;
  font-size: 28px;
  color: #F7F7F7;
}

h2:hover:after {
  color: #C2CCD1;
}

.footer {
  padding : 20px 0 20px 0;
  background-color: #F7F7F7;
}


/* http://prismjs.com/download.html?themes=prism&languages=markup+css+clike+javascript+bash+http+php+php-extras+sql */
/**
 * prism.js default theme for JavaScript, CSS and HTML
 * Based on dabblet (http://dabblet.com)
 * @author Lea Verou
 */

code[class*="language-"],
pre[class*="language-"] {
  color: black;
  background: none;
  text-shadow: 0 1px white;
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;

  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;

  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

pre[class*="language-"]::-moz-selection, pre[class*="language-"] ::-moz-selection,
code[class*="language-"]::-moz-selection, code[class*="language-"] ::-moz-selection {
  text-shadow: none;
  background: #b3d4fc;
}

pre[class*="language-"]::selection, pre[class*="language-"] ::selection,
code[class*="language-"]::selection, code[class*="language-"] ::selection {
  text-shadow: none;
  background: #b3d4fc;
}

@media print {
  code[class*="language-"],
  pre[class*="language-"] {
    text-shadow: none;
  }
}

/* Code blocks */
pre[class*="language-"] {
  padding: 1em;
  margin: .5em 0;
  overflow: auto;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: #f5f2f0;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: .1em;
  border-radius: .3em;
  white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: slategray;
}

.token.punctuation {
  color: #999;
}

.namespace {
  opacity: .7;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #905;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #690;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #a67f59;
  background: hsla(0, 0%, 100%, .5);
}

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #07a;
}

.token.function {
  color: #DD4A68;
}

.token.regex,
.token.important,
.token.variable {
  color: #e90;
}

.token.important,
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

blockquote {
  font-size: 14px;
}
